@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{text-align:center}.App-logo{height:40vmin;pointer-events:none}@media(prefers-reduced-motion: no-preference){.App-logo{-webkit-animation:App-logo-spin infinite 20s linear;animation:App-logo-spin infinite 20s linear}}.App-header{background-color:#282c34;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:#fff}.App-link{color:#61dafb}@-webkit-keyframes App-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}@keyframes App-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}body{width:100vw;height:100vh;display:flex;align-items:center;justify-content:center;overflow:hidden;background-image:linear-gradient(45deg, #8B5CF6, #EC4899);font-family:"Montserrat",sans-serif}*{box-sizing:border-box;font-family:"Montserrat",sans-serif}
.carousel{position:relative;width:23rem;height:23rem;perspective:500px;transform-style:preserve-3d}
.loader{margin-top:20px}
.modal-background{position:relative;display:flex;justify-content:center;align-items:center}.modal-box{width:600px;border-radius:1rem;background-color:#f5f5f5}.modal-box .heading{display:flex;justify-content:space-between;align-items:center;padding:10px 15px}.modal-box .heading .title{margin:0px;font-family:"Montserrat",sans-serif;color:#1f2937;font-size:24px}.modal-box .content{padding:10px 15px;color:#9ca3af;border-bottom:1px solid #d3d3d3}.modal-box .content:last-child{border-bottom:none}.closeBtn{color:crimson;font-size:24px;position:absolute;top:10px;right:10px}.closeBtn:hover{cursor:pointer}.center-modal{position:fixed;top:50%;left:50%;transform:translate(-50%, -50%);z-index:1000}
.card-container{position:absolute;width:100%;max-height:100%;transform:rotateY(calc(var(--offset) * 50deg)) scaleY(calc(1 + var(--abs-offset) * -0.4)) translateZ(calc(var(--abs-offset) * -30rem)) translateX(calc(var(--direction) * -5rem));-webkit-filter:blur(calc(var(--abs-offset) * 1rem));filter:blur(calc(var(--abs-offset) * 1rem));transition:all .3s ease-out}.card{width:100%;height:100%;padding:2rem;background-color:hsl(280deg, 40%, calc(100% - var(--abs-offset) * 50%));border-radius:1rem;color:#9ca3af;text-align:justify;transition:all .3s ease-out;display:flex;flex-direction:column;align-items:center}.card h2{text-align:center;font-weight:bold;margin:0 0 .7em;color:#1f2937}.card p,.card h2{transition:all .3s ease-out;opacity:var(--active)}.nav{color:#fff;font-size:5rem;position:absolute;display:flex;align-items:center;justify-content:center;top:50%;z-index:-1;cursor:pointer;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;background:unset;border:unset}.nav.left{transform:translateX(-100%) translateY(-50%)}.nav.right{right:0;transform:translateX(100%) translateY(-50%)}.comments_btn{color:#fff;text-transform:uppercase;font-weight:600;background-color:#ec4899;margin-top:auto;display:flex;justify-content:center;align-items:center;border:none;border-radius:10px;width:150px;height:30px}.comments_btn:hover{cursor:pointer}
.newpost__inpt{margin-bottom:10px;padding:5px;width:100%;border:1px solid #ccc;border-radius:5px}.newpost__inpt:hover{border-color:#9ca3af}.newpost__inpt:focus{border:1px solid #858585;outline:none}.newpost_container{position:absolute;display:flex;flex-direction:column;justify-content:center;align-items:center;transform:translateX(-13%)}.newpost_title{margin-top:20px;color:#fff;text-transform:uppercase;font-weight:600;margin-bottom:10px}.newpost_window{background-color:#fff;width:500px;max-height:300px;border-radius:1rem;overflow-y:auto}.newpost_window .newpost_content{display:flex;flex-direction:column;align-items:flex-start;padding:20px 20px 0;margin-bottom:10px}.newpost_window .newpost_content p{margin:0}.newpost_window .newpost_content button{background-color:#ec4899;color:#fff;border:none;border-radius:5px;padding:5px 10px;cursor:pointer;margin:0 auto}.message-container{display:flex;justify-content:center;align-items:center;margin-bottom:10px}.message{margin:0;text-align:center}.error-message{color:red}.success-message{color:#00d700}
